
.ql {
    color: #555;
}
.qlGreen {
    color: rgb(89,182,91);
}
.qlBlue {
    color: rgb(89,91,182);
}
.qlRed {
    color: rgb(182,89,91);
}
.ql, .qlGreen, .qlBlue, .qlRed {
    text-decoration: none;
}
.qlFlex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 3px;
}
.qlTz {
    font-family: "Roboto","Helvetica","Arial",sans-serif;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 0;
    display: inline-block;
    position: relative;
    margin: 0;
    vertical-align: middle;
    outline: none;
    line-height: 36px;
    text-align: center;
}
.qlNoHover {
    padding-right: 3px;
}
.qlNoHover:hover, .qlNoHover:active {
    background-color: inherit !important;
    cursor: default !important;
}
[class*='fgrid'],
  [class*='fgrid-col-'],
  [class*='mobile-'],
  .grid:after {
  	box-sizing: border-box;
}
[class*='fgrid-col-'] {
	   float: left;
     min-height: 1px;
     padding-right: 20px; /* column-space */
}
[class*='fgrid-col-'] [class*='fgrid-col-']:last-child {
    padding-right: 0;
}
.fgrid {
  	width: 100%;
  	max-width: 1140px;
  	min-width: 748px; /* when using padded grid on ipad in portrait mode, width should be viewport-width - padding = (768 - 20) = 748. actually, it should be even smaller to allow for padding of grid containing element */
  	margin: 0 auto;
  	overflow: hidden;
}
.fgrid:after {
  	content: "";
  	display: table;
  	clear: both;
}
.fgrid-col-512-12 {
    width: 44.444441667%;
}
.fgrid-col-1-12 {
    width: 11.110833333%
}
#rangeField {
    height: 80px;
}
#rangeField h6 {
    margin-bottom: 7px;
    margin-top: 10px;
}
@media handheld, only screen and (max-width: 767px) {
.fgrid {
  		width: 100%;
  		min-width: 0;
  		margin-left: 0;
  		margin-right: 0;
  		padding-left: 20px; /* grid-space to left */
  		padding-right: 10px; /* grid-space to right: (grid-space-left - column-space) e.g. 20px-10px=10px */
}
[class*='fgrid-col-'] {
      width: auto;
      float: none;
      margin: 10px 0;
      padding-left: 0;
      padding-right: 10px; /* column-space */
}
[class*='fgrid-col-'] [class*='fgrid-col-'] {
      padding-right: 0;
}
#rangeField {
      height: 145px;
}
}
.slider {
    padding-left:20px;
    min-width: 260px;
}
.flatpickr {
    width: 170px;
    border: 1px solid #757575;
    border-radius: 2px;
}
.centered {
    width: 260px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 5px;
}
.tp-label {
    display: inline-block;
    width: 80px;
}



