
.loading:after {
  content: "Loading...";
  padding-top: 12px;
  padding-bottom: 12px;
  display: inline-block;
  margin: auto;
  text-align: center;
  position: relative;
  left: 10px;
  font-size: 20px;
}
.glicko-up {
  color: rgb(89,170,91);
  font-weight: bold;
}
.glicko-down {
  color: rgb(182, 89, 91);
  font-weight: bold;
}
.pos-up, .pos-down, .pos-same {
  width: 25px;
  margin-left: 0px;
}
.pos-up {
  display:inline-block;
  color: rgb(89,170,91);
  font-weight: bold;
}
.pos-up:before {
  content: '⬆';
}
.pos-down {
  display:inline-block;
  color: rgb(182, 89, 91);
  font-weight: bold;
}
.pos-down:before {
  content: '⬇';
}
.pos-same {
  display: inline-block;
  color: rgb(182,182,182);
  font-weight: bold;
}
.pos-same:before {
  content: '–';
}
tbody tr:nth-child(even) {
  background-color: rgb(248,248,248);
}



