
.leaflet-container {
  background: #fff;
}
#map {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1;
}
.maptimer {
  background-color: rgba(255, 255, 255, 0.6);
  border: none;
  padding: 5px;
  padding-top: 0;
  padding-bottom: 0;
}
.leaflet-tooltip-left.maptimer::before {
  border-left: none;
}
.leaflet-tooltip-right.maptimer::before {
  border-right: none;
}



