
.server-select {
  position: absolute;
  top: 20px;
  left: calc(50% - 110px);
  z-index: 9999;
  height: 30px;
  width: 220px;
  background-color: rgba(230,230,230,.62);
  border-radius: 8px;
}
.server-select div {
  display: inline-block;
  margin-top: 4px;
  margin-left: 12px;
}
.map-select {
  width: 140px;
}



